import { Call } from "./Call";
import { HTMLUtils } from "../genb/base/utils/HTMLUtils";
import { BaseUtils } from "../genb/base/utils/BaseUtils";
import { StringUtils } from "../genb/base/utils/StringUtils";
import { Stats } from "./Stats";
import { StatsReport } from "./StatsReport";
import { Util } from "./Util";
import { SDPUtils } from "./SDPUtils";
import { UIConstants } from "./UIConstants";

export class InfoBox {

  private errorMessages: Array<string>;
  private warningMessages: Array<string>;

  private startTime: number;
  private connectTime: number;
  private stats: Stats;
  private prevStats: Stats;
  private getStatsTimer: any;

  private localTrackIds: any;
  private remoteTrackIds: any;
  private iceCandidateTypes: any;

  private localDecodedFrames: number;
  private localStartTime: number;
  private remoteDecodedFrames: number;
  private remoteStartTime: number;
  private endTime: number;

  private call: Call;

  private infoDiv: HTMLElement;
  private remoteVideo: HTMLElement;
  private localVideo: HTMLElement;
  private versionInfo: any;

  constructor(infoDiv: HTMLElement, call: Call, versionInfo: any) {
    this.call = call;
    this.infoDiv = infoDiv;
    this.remoteVideo = HTMLUtils.get(UIConstants.remoteVideo);
    this.localVideo = HTMLUtils.get(UIConstants.localVideo);
    this.versionInfo = versionInfo;
    this.errorMessages = new Array<string>();
    this.warningMessages = new Array<string>();
    // Time when the call was intiated and accepted.
    this.startTime = null;
    this.connectTime = null;
    this.stats = null;
    this.prevStats = null;
    this.getStatsTimer = null;
    this.localTrackIds = {
      video: '',
      audio: ''
    };
    this.remoteTrackIds = {
      video: '',
      audio: ''
    };

    // Types of gathered ICE Candidates.
    this.iceCandidateTypes = {
      Local: {},
      Remote: {}
    };

    // Used to calculate FPS for the video element.
    this.localDecodedFrames = 0;
    this.localStartTime = 0;
    this.localVideo.addEventListener('playing', (event: any): void => {
      this.localDecodedFrames = event.target.webkitDecodedFrameCount;
      this.localStartTime = new Date().getTime();
    });

    this.remoteDecodedFrames = 0;
    this.remoteStartTime = 0;
    this.remoteVideo.addEventListener('playing', (event: any): void => {
      this.remoteDecodedFrames = event.target.webkitDecodedFrameCount;
      this.remoteStartTime = new Date().getTime();
    });
  };

  public pushErrorMessage(msg: string): void {
    this.errorMessages.push(msg);
    this.updateInfoDiv();
    this.showInfoDiv();
  }

  public pushWarningMessage(msg:string):void {
    this.warningMessages.push(msg);
    this.updateInfoDiv();
    this.showInfoDiv();
  };

  public recordIceCandidateTypes(data:any):void {
    const location:string = data.location;
    const candidate:string = data.candidate;
    var type:string = SDPUtils.iceCandidateType(candidate);
  
    var types = this.iceCandidateTypes[location];
    if (!types[type]) {
      types[type] = 1;
    } else {
      ++types[type];
    }
    this.updateInfoDiv();
  };

  public updateInfoDiv(): void {
    var contents = '<pre id="info-box-stats" style="line-height: initial">';

    if (BaseUtils.isObjectDefined(this.stats)) {
      var states = this.call.getPeerConnectionStates();
      if (!BaseUtils.isObjectDefined(states)) {
        return;
      }
      // Build the display.
      contents += this.buildLine('States');
      contents += this.buildLine('Signaling', states.signalingState);
      contents += this.buildLine('Gathering', states.iceGatheringState);
      contents += this.buildLine('Connection', states.iceConnectionState);
      for (var endpoint in this.iceCandidateTypes) {
        var types = [];
        for (var type in this.iceCandidateTypes[endpoint]) {
          types.push(type + ':' + this.iceCandidateTypes[endpoint][type]);
        }
        contents += this.buildLine(endpoint, types.join(' '));
      }
      var statReport: any = Stats.enumerateStats(this.stats, this.localTrackIds,
        this.remoteTrackIds);

      var connectionStats = statReport.connection;
      var localAddr;
      var remoteAddr;
      var localAddrType;
      var remoteAddrType;
      var localPort;
      var remotePort;
      if (connectionStats) {
        localAddr = connectionStats.localIp;
        remoteAddr = connectionStats.remoteIp;
        localAddrType = connectionStats.localType;
        remoteAddrType = connectionStats.remoteType;
        localPort = connectionStats.localPort;
        remotePort = connectionStats.remotePort;
      }
      if (localAddr && remoteAddr) {
        var localCandId = connectionStats.localCandidateId;
        var localTypePref;
        if (localCandId) {
          localTypePref = connectionStats.localPriority >> 24;
        }
        contents += this.buildLine('LocalAddr', localAddr +
          ' (' + localAddrType + (typeof localTypePref !== undefined ? '' +
            SDPUtils.formatTypePreference(localTypePref) : '') + ')');
        contents += this.buildLine('LocalPort', localPort);
        contents += this.buildLine('RemoteAddr', remoteAddr + ' (' +
          remoteAddrType + ')');
        contents += this.buildLine('RemotePort', remotePort);
      }
      contents += this.buildLine();

      contents += this.buildStatsSection();
    }

    if (this.errorMessages.length > 0 || this.warningMessages.length > 0) {
      contents += this.buildLine('\nMessages');
      if (this.errorMessages.length) {
        this.infoDiv.classList.add('warning');
        for (var i = 0; i !== this.errorMessages.length; ++i) {
          contents += this.errorMessages[i] + '\n';
        }
      } else {
        this.infoDiv.classList.add('active');
        for (var j = 0; j !== this.warningMessages.length; ++j) {
          contents += this.warningMessages[j] + '\n';
        }
      }
    } else {
      this.infoDiv.classList.remove('warning');
    }

    if (StringUtils.isNotEmpty(this.versionInfo)) {
      contents += this.buildLine();
      contents += this.buildLine('Version');
      for (var key in this.versionInfo) {
        contents += this.buildLine(key, this.versionInfo[key]);
      }
    }

    contents += '</pre>';

    if (this.infoDiv.innerHTML !== contents) {
      this.infoDiv.innerHTML = contents;
    }
  };

  private buildLine(label?: string, value?: string): string {
    let columnWidth: number = 12;
    let line: string = '';
    if (StringUtils.isNotEmpty(label)) {
      line += label + ':';
      while (line.length < columnWidth) {
        line += ' ';
      }

      if (StringUtils.isNotEmpty(value)) {
        line += value;
      }
    }
    line += '\n';
    return line;
  };

  private buildStatsSection(): any {
    let contents:string = this.buildLine('Stats');
    let statReport: StatsReport = Stats.enumerateStats(this.stats, this.localTrackIds,
      this.remoteTrackIds);
    let prevStatReport: StatsReport = Stats.enumerateStats(this.prevStats, this.localTrackIds,
      this.remoteTrackIds);

    // Obtain setup and latency this.stats_.
    var totalRtt = statReport.connection.totalRoundTripTime * 1000;
    var currentRtt = statReport.connection.currentRoundTripTime * 1000;

    if (this.endTime !== null) {
      contents += this.buildLine('Call time',
        InfoBox.formatInterval(window.performance.now() - this.connectTime));
      contents += this.buildLine('Setup time',
        InfoBox.formatMsec(this.connectTime - this.startTime));
    }
    if (statReport.connection.remoteIp !== '') {
      contents += this.buildLine('TotalRtt', InfoBox.formatMsec(totalRtt));
      contents += this.buildLine('CurrentRtt', InfoBox.formatMsec(currentRtt));
    }

    const rxAudio:any = statReport.audio.remote;
    let rxPrevAudio = prevStatReport.audio.remote;
    let rxPrevVideo = prevStatReport.video.remote;
    const rxVideo:any = statReport.video.remote;
    let txAudio = statReport.audio.local;
    let txPrevAudio = prevStatReport.audio.local;
    let txPrevVideo = prevStatReport.video.local;
    const txVideo:any = statReport.video.local;

    let rxAudioBitrate;
    let rxAudioClockRate;
    let rxAudioCodec;
    let rxAudioJitter;
    let rxAudioLevel;
    let rxAudioPacketRate;
    let rxAudioPlType;
    let rxVideoBitrate;
    let rxVideoCodec;
    let rxVideoDroppedFrames;
    let rxVideoFirCount;
    let rxVideoFps;
    let rxVideoHeight;
    let rxVideoNackCount;
    let rxVideoPacketRate;
    let rxVideoPliCount;
    let rxVideoPlType;

    let txAudioBitrate;
    let txAudioClockRate;
    let txAudioCodec;
    let txAudioLevel;
    let txAudioPacketRate;
    let txAudioPlType;
    let txVideoBitrate;
    let txVideoCodec;
    let txVideoFirCount;
    let txVideoFps;
    let txVideoHeight;
    let txVideoNackCount;
    let txVideoPacketRate;
    let txVideoPliCount;
    let txVideoPlType;

    if (txAudio.codecId !== '' && txAudio.payloadType !== 0) {
      txAudioCodec = txAudio.mimeType;
      txAudioLevel = parseFloat(txAudio.audioLevel).toFixed(3);
      txAudioClockRate = txAudio.clockRate;
      txAudioPlType = txAudio.payloadType;
      txAudioBitrate = Stats.computeBitrate(txAudio, txPrevAudio, 'bytesSent');
      txAudioPacketRate = Stats.computeRate(txAudio, txPrevAudio, 'packetsSent');
      contents += this.buildLine(
        'Audio Tx', txAudioCodec + '/' + txAudioPlType + ', ' +
        'rate ' + txAudioClockRate + ', ' +
        InfoBox.formatBitrate(txAudioBitrate) + ', ' +
        InfoBox.formatPacketRate(txAudioPacketRate) + ', inputLevel ' +
        txAudioLevel);
    }
    if (rxAudio.codecId !== '' && rxAudio.payloadType !== 0) {
      rxAudioCodec = rxAudio.mimeType;
      rxAudioLevel = parseFloat(rxAudio.audioLevel).toFixed(3);
      rxAudioJitter = parseFloat(rxAudio.jitter).toFixed(3);
      rxAudioClockRate = rxAudio.clockRate;
      rxAudioPlType = rxAudio.payloadType;
      rxAudioBitrate = Stats.computeBitrate(rxAudio, rxPrevAudio, 'bytesReceived');
      rxAudioPacketRate = Stats.computeRate(rxAudio, rxPrevAudio, 'packetsReceived');
      contents += this.buildLine(
        'Audio Rx', rxAudioCodec + '/' + rxAudioPlType + ', ' +
        'rate ' + rxAudioClockRate + ', ' +
        'jitter ' + rxAudioJitter + ', ' +
        InfoBox.formatBitrate(rxAudioBitrate) + ', ' +
        InfoBox.formatPacketRate(rxAudioPacketRate) + ', outputLevel ' +
        rxAudioLevel);
    }
    if (txVideo.codecId !== '' && txVideo.payloadType !== 0 &&
      txVideo.frameHeight !== 0) {
      txVideoCodec = txVideo.mimeType;
      txVideoHeight = txVideo.frameHeight;
      txVideoPlType = txVideo.payloadType;
      txVideoPliCount = txVideo.pliCount;
      txVideoFirCount = txVideo.firCount;
      txVideoNackCount = txVideo.nackCount;
      txVideoFps = Util.calculateFps(this.remoteVideo, this.remoteDecodedFrames,
        this.remoteStartTime, 'local', this.updateDecodedFramesCallback);
      txVideoBitrate = Stats.computeBitrate(txVideo, txPrevVideo, 'bytesSent');
      txVideoPacketRate = Stats.computeRate(txVideo, txPrevVideo, 'packetsSent');
      contents += this.buildLine('Video Tx',
        txVideoCodec + '/' + txVideoPlType + ', ' + txVideoHeight.toString() +
        'p' + txVideoFps.toString() + ', ' +
        'firCount ' + txVideoFirCount + ', ' +
        'pliCount ' + txVideoPliCount + ', ' +
        'nackCount ' + txVideoNackCount + ', ' +
        InfoBox.formatBitrate(txVideoBitrate) + ', ' +
        InfoBox.formatPacketRate(txVideoPacketRate));
    }
    if (rxVideo.codecId !== '' && rxVideo.payloadType !== 0 &&
      txVideo.frameHeight !== 0) {
      rxVideoCodec = rxVideo.mimeType;
      rxVideoHeight = rxVideo.frameHeight;
      rxVideoPlType = rxVideo.payloadType;
      rxVideoDroppedFrames = rxVideo.framesDropped;
      rxVideoPliCount = rxVideo.pliCount;
      rxVideoFirCount = rxVideo.firCount;
      rxVideoNackCount = rxVideo.nackCount;
      rxVideoFps = Util.calculateFps(this.remoteVideo, this.remoteDecodedFrames,
        this.remoteStartTime, 'remote', this.updateDecodedFramesCallback);
      rxVideoBitrate = Stats.computeBitrate(rxVideo, rxPrevVideo, 'bytesReceived');
      rxVideoPacketRate = Stats.computeRate(rxVideo, rxPrevVideo, 'packetsReceived');
      contents += this.buildLine('Video Rx',
        rxVideoCodec + '/' + rxVideoPlType + ', ' + rxVideoHeight.toString() +
        'p' + rxVideoFps.toString() + ', ' +
        'firCount ' + rxVideoFirCount + ', ' +
        'pliCount ' + rxVideoPliCount + ', ' +
        'nackCount ' + rxVideoNackCount + ', ' +
        'droppedFrames ' + rxVideoDroppedFrames + ', ' +
        InfoBox.formatBitrate(rxVideoBitrate) + ', ' +
        InfoBox.formatPacketRate(rxVideoPacketRate));
    }
    return contents;
  };

  private showInfoDiv(): void {
    this.getStatsTimer = setInterval(this.refreshStats.bind(this), 1000);
    this.refreshStats();
    this.infoDiv.classList.add('active');
  };

  private refreshStats(): void {
    this.call.getPeerConnectionStats((response: any): void => {
      this.prevStats = this.stats;
      this.stats = response;
      this.updateInfoDiv();
    });
  };

  /**
   * Callback that sets used to keep track for calculating FPS for video elements. 
   * 
   * @private
   * @param {number} decodedFrames 
   * @param {number} startTime 
   * @param {string} remoteOrLocal 
   * @memberof InfoBox
   */
  private updateDecodedFramesCallback(
    decodedFrames: number, startTime: number, remoteOrLocal: string) {
    if (remoteOrLocal === 'local') {
      this.localDecodedFrames = decodedFrames;
      this.localStartTime = startTime;
    } else if (remoteOrLocal === 'remote') {
      this.remoteDecodedFrames = decodedFrames;
      this.remoteStartTime = startTime;
    }
  };

  public getRemoteTrackIds(stream:MediaStream):void {
    stream.getTracks().forEach((track:any):void => {
      if (track.kind === 'audio') {
        this.remoteTrackIds.audio = track.id;
      } else if (track.kind === 'video') {
        this.remoteTrackIds.video = track.id;
      }
    });
  };

  public getLocalTrackIds(stream:MediaStream):void {
    stream.getTracks().forEach((track:any):void => {
      if (track.kind === 'audio') {
        this.localTrackIds.audio = track.id;
      } else if (track.kind === 'video') {
        this.localTrackIds.video = track.id;
      }
    });
  };

  public setSetupTimes(startTime:number, connectTime:number):void {
    this.startTime = startTime;
    this.connectTime = connectTime;
  };

  /**
   * Convert a number of milliseconds into a '[HH:]MM:SS' string. 
   * 
   * @private
   * @static
   * @param {number} value 
   * @returns {string} 
   * @memberof InfoBox
   */
  private static formatInterval(value: number): string {
    let result: string = '';
    let seconds: number = Math.floor(value / 1000);
    let minutes: number = Math.floor(seconds / 60);
    let hours: number = Math.floor(minutes / 60);
    let formatTwoDigit: Function = (twodigit: number): string => {
      return ((twodigit < 10) ? '0' : '') + twodigit.toString();
    };

    if (hours > 0) {
      result += formatTwoDigit(hours) + ':';
    }
    result += formatTwoDigit(minutes - hours * 60) + ':';
    result += formatTwoDigit(seconds - minutes * 60);
    return result;
  };

  /**
   * Convert a number of milliesconds into a 'XXX ms' string. 
   * 
   * @private
   * @static
   * @param {number} value 
   * @returns {string} 
   * @memberof InfoBox
   */
  private static formatMsec(value: number): string {
    return value.toFixed(0).toString() + ' ms';
  };

  /**
   * Convert a bitrate into a 'XXX Xbps' string.
   * 
   * @private
   * @static
   * @param {number} value 
   * @returns {string} 
   * @memberof InfoBox
   */
  private static formatBitrate(value: number): string {
    if (!value) {
      return '- bps';
    }

    let suffix: string;
    if (value < 1000) {
      suffix = 'bps';
    } else if (value < 1000000) {
      suffix = 'kbps';
      value /= 1000;
    } else {
      suffix = 'Mbps';
      value /= 1000000;
    }

    return value.toPrecision(3) + ' ' + suffix;
  };

  /**
   * Convert a packet rate into a 'XXX pps' string.
   * 
   * @private
   * @static
   * @param {number} value 
   * @returns {string} 
   * @memberof InfoBox
   */
  private static formatPacketRate(value: number): string {
    if (!value) {
      return '- pps';
    }
    return value.toPrecision(3) + ' ' + 'pps';
  };
}