import { Util } from "./Util";
import { UIConstants } from "./UIConstants";
import { HTMLUtils } from "../genb/base/utils/HTMLUtils";
import { Call } from "./Call";
import { BaseUtils } from "../genb/base/utils/BaseUtils";
import { IconSet } from "./IconSet";
import { HTMLTemplates } from "./HTMLTemplates";
import { StringUtils } from "../genb/base/utils/StringUtils";
import { LoadingParams } from "./LoadingParams";
import { CallEvent } from "./CallEvent";
import { InfoBox } from "./InfoBox";
import { RoomSelection } from "./RoomSelection";
import { RoomSelectionEvent } from "./RoomSelectionEvent";
import { InvitationView } from "./InvitationView";
import { AppControllerEvent } from "./AppControllerEvent";
import { EventDispatcherService } from "../../../../web_plugin/ts/com/genb/base/services/EventDispatcherService";

/**
 * Quick Hellou controller.
 * 
 * @export
 * @class AppController
 */
export class AppController extends EventDispatcherService {

  private hangupSvg: HTMLElement;
  private icons: HTMLElement;
  private localVideo: HTMLVideoElement;
  private miniVideo: HTMLVideoElement;
  private sharingDiv: HTMLElement;
  private statusDiv: HTMLElement;
  private remoteVideo: HTMLVideoElement;
  private videosDiv: HTMLElement;
  private roomLinkHref: HTMLLinkElement;
  private roomLinkHrefWaiting: HTMLLinkElement;
  private rejoinDiv: HTMLElement;
  private textChatButton: HTMLElement;
  private rejoinButton: HTMLElement;
  private newRoomButton: HTMLElement;
  private fullscreenIcon: HTMLElement;
  private hellouLogoFooterDiv: HTMLElement;
  private overlayDiv: HTMLElement;
  private overlayWaitingDiv: HTMLElement;
  private closeOverlayButton: HTMLElement;
  private textChatWindow: HTMLElement;
  private textChatCloseButton: HTMLElement;
  private textChatCollapseButton: HTMLElement;
  private textChatMessageList: HTMLElement;
  private textChatInput: HTMLInputElement;

  private loadingParams: LoadingParams;

  private call: Call;
  private roomSelection: RoomSelection;
  private infoBox: InfoBox;

  // to revise

  private roomLink: string;
  private localStream: MediaStream;

  // helpers

  private hideIconsAfterTimeout: number;
  private remoteVideoResetTimer: any;
  private muteAudioIconSet: IconSet;
  private muteVideoIconSet: IconSet;
  private fullscreenIconSet: IconSet;

  /**
   * Creates an instance of AppController.
   * @memberof AppController
   */
  constructor(loadingParams: LoadingParams) {
    super();
    this.loadingParams = loadingParams;
    this.loadUrlParams();
  }
  public init(): void {
    Util.trace('Initializing; Server ' + (BaseUtils.isObjectDefined(this.loadingParams.roomServer) ? `= ${this.loadingParams.roomServer}` : "not defined") + '.');
    Util.trace('Initializing; Room ID ' + (BaseUtils.isObjectDefined(this.loadingParams.roomID) ? `= ${this.loadingParams.roomID}` : "not defined") + '.');

    this.hangupSvg = HTMLUtils.get(UIConstants.hangupSvg);
    this.icons = HTMLUtils.get(UIConstants.icons);
    this.localVideo = HTMLUtils.get(UIConstants.localVideo) as HTMLVideoElement;
    this.miniVideo = HTMLUtils.get(UIConstants.miniVideo) as HTMLVideoElement;
    this.sharingDiv = HTMLUtils.get(UIConstants.sharingDiv);
    this.statusDiv = HTMLUtils.get(UIConstants.statusDiv);
    this.remoteVideo = HTMLUtils.get(UIConstants.remoteVideo) as HTMLVideoElement;
    this.videosDiv = HTMLUtils.get(UIConstants.videosDiv);
    this.roomLinkHref = HTMLUtils.get(UIConstants.roomLinkHref) as HTMLLinkElement;
    this.roomLinkHrefWaiting = HTMLUtils.get(UIConstants.roomLinkHrefWaiting) as HTMLLinkElement;
    this.rejoinDiv = HTMLUtils.get(UIConstants.rejoinDiv);
    this.textChatButton = HTMLUtils.get(UIConstants.textChatButton);
    this.rejoinButton = HTMLUtils.get(UIConstants.rejoinButton);
    this.newRoomButton = HTMLUtils.get(UIConstants.newRoomButton);
    this.fullscreenIcon = HTMLUtils.get(UIConstants.fullscreenSvg);
    this.hellouLogoFooterDiv = HTMLUtils.get(UIConstants.hellouLogoFooterDiv);
    this.overlayDiv = HTMLUtils.get(UIConstants.overlayDiv);
    this.overlayWaitingDiv = HTMLUtils.get(UIConstants.overlayWaitingDiv);
    this.closeOverlayButton = HTMLUtils.get(UIConstants.closeOverlayButton);
    this.textChatWindow = HTMLUtils.get(UIConstants.textChat.chatWindow);
    this.textChatCloseButton = HTMLUtils.get(UIConstants.textChat.closeButton);
    this.textChatCollapseButton = HTMLUtils.get(UIConstants.textChat.collapseButton);
    this.textChatMessageList = HTMLUtils.get(UIConstants.textChat.messageList);
    this.textChatInput = HTMLUtils.get(UIConstants.textChat.input) as HTMLInputElement;

    this.newRoomButton.addEventListener('click', (event: MouseEvent): void => { this.onNewRoomClick() }, false);
    this.rejoinButton.addEventListener('click', (event: MouseEvent): void => { this.onRejoinClick() }, false);
    this.closeOverlayButton.addEventListener('click', (event: MouseEvent): void => { this.onCloseOverlayClick() }, false);
    this.textChatButton.addEventListener('click', (event: MouseEvent): void => { this.onTextChatButtonClick() }, false);
    this.textChatCloseButton.addEventListener('click', (event: MouseEvent): void => { this.onTextChatCloseButtonClick() }, false);
    this.textChatCollapseButton.addEventListener('click', (event: MouseEvent): void => { this.onTextChatCollapseButtonClick() }, false);
    this.textChatInput.addEventListener('keyup', (event: KeyboardEvent): void => { this.onTextChatInputKeyUp(event); }, false);

    let sendTextChatMessageButton: HTMLElement = HTMLUtils.get(".text-chat-window__send-message-button");
    sendTextChatMessageButton.addEventListener("click", (event) => { this.sendTextChatMessageButtonClickHandler() });

    let overlayButtonsList: Array<HTMLElement> = HTMLUtils.array(".button--send-invitation");

    let sendInvitationCloserButtonList: Array<HTMLElement> = HTMLUtils.array('.overlay__closer-button');
    sendInvitationCloserButtonList.forEach((sendInvitationCloserButton) => {
      sendInvitationCloserButton.addEventListener("click", (event): void => {
        this.closeOverlay(HTMLUtils.get(`.${sendInvitationCloserButton.dataset.screen}`),
          HTMLUtils.array(`.button--overlay[data-screen='${sendInvitationCloserButton.dataset.screen}']`));
      });
    });

    overlayButtonsList.forEach((overlayButton: HTMLElement): void => {
      const screen: string = overlayButton.dataset.screen;
      const overlaySelector: string = `.${screen}`;
      const selectedOverlayButtonsList: Array<HTMLElement> = HTMLUtils.array(`.button--overlay[data-screen='${screen}']`)
      overlayButton.onclick = (): void => {
        this.closeAllOverlays();
        let sendInvitationOverlay: HTMLElement = HTMLUtils.get(overlaySelector);
        sendInvitationOverlay.classList.remove("hidden");

        let sendInvitationResult: HTMLElement = HTMLUtils.get(`.overlay--${screen}__result`);
        sendInvitationResult.classList.add("hidden");
        let sendInvitationResultMessage: HTMLElement = HTMLUtils.get(`.overlay--${screen}__result-message`);
        sendInvitationResultMessage.innerHTML = "";
        let sendInvitationBody: HTMLElement = HTMLUtils.get(`.overlay--${screen}__body`);
        sendInvitationBody.classList.remove("hidden");
        selectedOverlayButtonsList.forEach((inviteButton: HTMLElement): void => {
          inviteButton.classList.add("js-active");
        });
      }
    });

    const schedulerButton: HTMLElement = HTMLUtils.get(".button--setup-future-talk");
    schedulerButton.addEventListener("click", (event: any) => {
      let setupFutureTalkOverlay: HTMLElement = HTMLUtils.get(".overlay--setup-future-talk");
      if (BaseUtils.isObjectDefined(setupFutureTalkOverlay))
        setupFutureTalkOverlay.classList.remove("hidden");
    });

    // send invitation view
    new InvitationView(HTMLUtils.get(".send-invitation"));

    // chat window handlers
    let chatWindowInput: HTMLInputElement = HTMLUtils.get(".text-chat-window__message-input") as HTMLInputElement;
    chatWindowInput.addEventListener("focusin", (event): void => {
      if (chatWindowInput.value === "Type your message here")
        chatWindowInput.value = "";
    });

    chatWindowInput.addEventListener("focusout", (event): void => {
      if (StringUtils.isEmpty(chatWindowInput.value))
        chatWindowInput.value = "Type your message here";
    });

    this.muteAudioIconSet = new IconSet(UIConstants.muteAudioSvg);
    this.muteVideoIconSet = new IconSet(UIConstants.muteVideoSvg);
    this.fullscreenIconSet = new IconSet(UIConstants.fullscreenSvg);

    this.roomLink = StringUtils.EMPTY;
    this.roomSelection = null;
    this.localStream = null;
    this.remoteVideoResetTimer = null;
    // If the params has a roomID specified, we should connect to that room
    // immediately. If not, show the room selection UI.
    if (this.loadingParams.roomID) {

      this.updateInvitationLinks(this.loadingParams.roomID);

      if (StringUtils.equals(this.loadingParams.additionalParam, "setup"))
        this.roomSelectedHander(this.loadingParams.roomID);
      else {
        this.createCall();

        // Ask the user to confirm.
        if (!RoomSelection.matchRandomRoomPattern(this.loadingParams.roomID)) {
          // Show the room name only if it does not match the random room pattern.
          let confirmJoinRoomSpanElement: HTMLElement = HTMLUtils.get(UIConstants.confirmJoinRoomSpan);
          confirmJoinRoomSpanElement.textContent = ' "' + this.loadingParams.roomID + '"';
        }
        var confirmJoinDiv: HTMLElement = HTMLUtils.get(UIConstants.confirmJoinDiv);
        this.show(confirmJoinDiv);

        let confirmJoinButtonElement: HTMLElement = HTMLUtils.get(UIConstants.confirmJoinButton);
        confirmJoinButtonElement.addEventListener("click", (): void => {
          this.hide(confirmJoinDiv);
          this.finishCallSetup(this.loadingParams.roomID);
        });

        if (this.loadingParams.bypassJoinConfirmation) {
          this.hide(confirmJoinDiv);
          this.finishCallSetup(this.loadingParams.roomID);
        }
      }
    } else {
      // Display the room selection UI.
      this.showRoomSelection();
    }

    window.addEventListener('online', (event: Event): void => { this.updateOnlineStatus(event) });
    window.addEventListener('offline', (event: Event): void => { this.updateOnlineStatus(event); });

    if (StringUtils.isNotEmpty(this.loadingParams.additionalParam)) {
      if (StringUtils.equals(this.loadingParams.additionalParam, "invite"))
        this.openSendInvitationOverlay();
    }

    this.callConsole();

    this.dispatchEvent(AppControllerEvent.INITIALIZED);
  }

  private openSendInvitationOverlay() {
    let overlayButtonsList: Array<HTMLElement> = HTMLUtils.array(".button--overlay");
    overlayButtonsList.forEach((overlayButton: HTMLElement): void => {
      const screen: string = "send-invitation";
      const overlaySelector: string = `.${screen}`;
      const selectedOverlayButtonsList: Array<HTMLElement> = HTMLUtils.array(`.button--overlay[data-screen='${screen}']`)
      this.closeAllOverlays();
      let sendInvitationOverlay: HTMLElement = HTMLUtils.get(overlaySelector);
      sendInvitationOverlay.classList.remove("hidden");

      let sendInvitationResult: HTMLElement = HTMLUtils.get(`.overlay--${screen}__result`);
      sendInvitationResult.classList.add("hidden");
      let sendInvitationResultMessage: HTMLElement = HTMLUtils.get(`.overlay--${screen}__result-message`);
      sendInvitationResultMessage.innerHTML = "";
      let sendInvitationBody: HTMLElement = HTMLUtils.get(`.overlay--${screen}__body`);
      sendInvitationBody.classList.remove("hidden");
      selectedOverlayButtonsList.forEach((inviteButton: HTMLElement): void => {
        inviteButton.classList.add("js-active");
      });
    });
  }

  private closeAllOverlays(): void {
    this.closeOverlay(HTMLUtils.get(`.send-invitation`),
      HTMLUtils.array(`.button--overlay[data-screen='send-invitation']`));
  }

  private closeOverlay(overlay: HTMLElement, buttons: Array<HTMLElement>): void {
    overlay.classList.add("hidden");
    buttons.forEach((inviteButton: HTMLElement): void => {
      inviteButton.classList.remove("js-active");
    });
  }

  private updateOnlineStatus(event: Event): void {
    const status: string = navigator.onLine ? "online" : "offline";
  }

  private showRoomSelection(): void {
    var roomSelectionDiv: HTMLElement = HTMLUtils.get(UIConstants.roomSelection.div);
    this.roomSelection = new RoomSelection(this.loadingParams.roomType);
    this.show(roomSelectionDiv);
    this.roomSelection.addEventListener(RoomSelectionEvent.ROOM_SELECTED,
      (data: any): void => { this.roomSelectedHander(data.roomID); }, this);
  };

  private roomSelectedHander(roomName: string): void {
    var roomSelectionDiv: HTMLElement = HTMLUtils.get(UIConstants.roomSelection.div);
    this.hide(roomSelectionDiv);
    this.createCall();
    this.finishCallSetup(roomName);

    if (BaseUtils.isObjectDefined(this.roomSelection)) {
      this.roomSelection.removeEventListeners();
      this.roomSelection = null;
    }

    // patch to set room url before local stream initialization 
    const roomT: string = `${window.location.protocol}//${window.location.host}/r/${roomName}`;
    this.pushCallNavigation(roomName, roomT);

    if (this.localStream) {
      this.attachLocalStream();
    }

    const schedulerLinkElement: HTMLLinkElement = HTMLUtils.get("footer .button--setup-future-talk") as HTMLLinkElement;
    schedulerLinkElement.href = `/scheduler/${roomName}`;
  }

  private updateClientsList() {
    Util.trace("===========================> updateClientsList");
    const clientsList = HTMLUtils.get(".clients-list ul");
    clientsList.insertAdjacentText("afterend", "<li>New client</li>");
  }

  private loadUrlParams(): void {
    const DEFAULT_VIDEO_CODEC: string = 'VP9';
    var urlParams: any = Util.queryStringToDictionary(window.location.search);
    this.loadingParams.audioSendBitrate = urlParams['asbr'];
    this.loadingParams.audioSendCodec = urlParams['asc'];
    this.loadingParams.audioRecvBitrate = urlParams['arbr'];
    this.loadingParams.audioRecvCodec = urlParams['arc'];
    this.loadingParams.opusMaxPbr = urlParams['opusmaxpbr'];
    this.loadingParams.opusFec = urlParams['opusfec'];
    this.loadingParams.opusDtx = urlParams['opusdtx'];
    this.loadingParams.opusStereo = urlParams['stereo'];
    this.loadingParams.videoSendBitrate = urlParams['vsbr'];
    this.loadingParams.videoSendInitialBitrate = urlParams['vsibr'];
    this.loadingParams.videoSendCodec = urlParams['vsc'];
    this.loadingParams.videoRecvBitrate = urlParams['vrbr'];
    this.loadingParams.videoRecvCodec = urlParams['vrc'] || DEFAULT_VIDEO_CODEC;
    this.loadingParams.videoFec = urlParams['videofec'];
  };

  private createCall(): void {
    var privacyLinks: HTMLElement = HTMLUtils.get(UIConstants.privacyLinks);
    this.hide(privacyLinks);
    this.call = new Call(this.loadingParams);
    this.infoBox = new InfoBox(HTMLUtils.get(UIConstants.infoDiv), this.call,
      this.loadingParams.versionInfo);

    var roomErrors = this.loadingParams.errorMessages;
    var roomWarnings = this.loadingParams.warningMessages;
    if (roomErrors && roomErrors.length > 0) {
      for (var i = 0; i < roomErrors.length; ++i) {
        this.infoBox.pushErrorMessage(roomErrors[i]);
      }
      return;
    } else if (roomWarnings && roomWarnings.length > 0) {
      for (var j = 0; j < roomWarnings.length; ++j) {
        this.infoBox.pushWarningMessage(roomWarnings[j]);
      }
    }

    this.call.addEventListener(CallEvent.REMOTE_HANGUP,
      this.onRemoteHangup, this);
    this.call.addEventListener(CallEvent.REMOTE_SDP_SET,
      (data: any): void => { this.onRemoteSdpSet(data.hasRemoteVideo); }, this);
    this.call.addEventListener(CallEvent.REMOTE_STREAM_ADDED,
      (data: any): void => { this.onRemoteStreamAdded(data.stream); }, this);
    this.call.addEventListener(CallEvent.LOCAL_STREAM_ADDED,
      (data: any): void => { this.onLocalStreamAdded(data.stream); }, this);
    this.call.addEventListener(CallEvent.SIGNALING_STATE_CHANGE,
      (): void => { this.infoBox.updateInfoDiv(); }, this);
    this.call.addEventListener(CallEvent.ICE_CONNECTION_STATE_CHANGE,
      (): void => { this.infoBox.updateInfoDiv(); }, this);
    this.call.addEventListener(CallEvent.NEW_ICE_CANDIDATE,
      (data: any): void => { this.infoBox.recordIceCandidateTypes(data) }, this);
    this.call.addEventListener(CallEvent.REMOTE_TEXT_CHAT_MESSAGE,
      (data: any): void => { this.onRemoteTextChatMessage(data.message) }, this);
    this.call.addEventListener(CallEvent.WSS_RESPONSE,
      (data: any): void => { this.onWssResponse(data) }, this);
    this.call.addEventListener(CallEvent.STATUS_MESSAGE,
      (message: string): void => { this.displayStatus(message) }, this);
    this.call.addEventListener(CallEvent.ERROR,
      (message: string): void => { this.displayError(message) }, this);
    this.call.addEventListener(CallEvent.CALLER_STARTED,
      this.displaySharingInfo, this);
    this.show(this.hellouLogoFooterDiv);
    this.show(this.videosDiv);
    this.show(this.overlayDiv);
  };

  private finishCallSetup(roomID: string): void {
    this.call.start(roomID);
    this.setupUI();

    // Call hangup with async = false. Required to complete multiple
    // clean up steps before page is closed.
    window.onbeforeunload = (): void => {
      this.call.hangup(false);
    };

    window.onpopstate = (event: any): void => {
      if (!event.state) {
        Util.trace('Reloading main page.');
        location.href = location.origin;
      } else {
        // This could be a forward request to open a room again.
        if (event.state.roomLink) {
          location.href = event.state.roomLink;
        }
      }
    };

  };

  private attachLocalStream(): void {
    Util.trace('Attaching local stream.');
    this.localVideo.srcObject = this.localStream;

    this.displayStatus('');
    this.activate(this.localVideo);
    this.show(this.icons);
    this.show(this.fullscreenIcon);

    if (this.localStream.getVideoTracks().length === 0) {
      const muteVideoSVGElement: HTMLElement = HTMLUtils.get(UIConstants.muteVideoSvg);
      this.hide(muteVideoSVGElement);
    }
    if (this.localStream.getAudioTracks().length === 0) {
      const muteAudioSVGElement: HTMLElement = HTMLUtils.get(UIConstants.muteAudioSvg);
      this.hide(muteAudioSVGElement);
    }
  };

  private recordIceCandidateTypes(data: any): void {
    this.infoBox.recordIceCandidateTypes(data);
  }

  private hangup(): void {
    Util.trace('Hanging up.');
    this.hide(this.icons);
    this.displayStatus('Hanging up');
    this.transitionToDone();

    // Call hangup with async = true.
    this.call.hangup(true);
    // Reset key and mouse event handlers.
    document.onkeypress = null;
    window.onmousemove = null;
  };

  private sendTextChatMessageButtonClickHandler(): void {
    var message = this.textChatInput.value.trim().replace(/(\r\n|\n|\r)/gm, "");
    if (StringUtils.isNotEmpty(message)) {
      this.addLocalMessageHTML(message);
      this.sendLocalChatMessage(message);
      this.textChatInput.value = "";
      this.textChatInput.focus();
    }
  }

  private onNewRoomClick(): void {
    console.log(location.origin);
    location.href = location.origin;
  };

  private onRejoinClick(): void {
    Util.trace("=============================> onRejoinClick");
    
    this.deactivate(this.rejoinDiv);
    this.hide(this.rejoinDiv);
    this.call.restart();
    this.setupUI();
  };

  setupUI(): void {
    this.iconEventSetup();
    window.addEventListener("mousemove", (event: MouseEvent): void => { this.showIcons(); })
    this.overlayDiv.addEventListener("mousemove", (event: MouseEvent): void => { this.showIcons(); })
    this.overlayDiv.addEventListener("click", (event: MouseEvent): void => { this.showIcons(); })

    const muteAudioSVGElement: HTMLElement = HTMLUtils.get(UIConstants.muteAudioSvg);
    muteAudioSVGElement.addEventListener("click", (event: MouseEvent): void => { this.toggleAudioMute(); })

    const muteVideoSVGElement: HTMLElement = HTMLUtils.get(UIConstants.muteVideoSvg);
    muteVideoSVGElement.addEventListener("click", (event: MouseEvent): void => { this.toggleVideoMute(); })

    const fullscreenSVGElement: HTMLElement = HTMLUtils.get(UIConstants.fullscreenSvg);
    fullscreenSVGElement.addEventListener("click", (event: MouseEvent): void => { this.toggleFullScreen(); })

    const hangupSVGElement: HTMLElement = HTMLUtils.get(UIConstants.hangupSvg);
    hangupSVGElement.addEventListener("click", (event: MouseEvent): void => { this.hangup(); })

    const textChatButtonElement: HTMLElement = HTMLUtils.get(UIConstants.textChatButton);
    textChatButtonElement.addEventListener("click", (event: MouseEvent): void => { this.toggleTextChatWindow(); });

    document.addEventListener("fullscreenchange", (event: MouseEvent): void => { this.onFullScreenChange() }, false);
    document.addEventListener("webkitfullscreenchange", (event: MouseEvent): void => { this.onFullScreenChange() }, false);
    document.addEventListener("mozfullscreenchange", (event: MouseEvent): void => { this.onFullScreenChange() }, false);

    Util.setUpFullScreen();
  };

  private toggleAudioMute(): void {
    this.call.toggleAudioMute();
    this.muteAudioIconSet.toggle();
  };

  private toggleTextChatWindow(): void {
    this.call.toggleTextChatWindow();
  };

  private toggleVideoMute(): void {
    this.call.toggleVideoMute();
    this.muteVideoIconSet.toggle();
  };

  onFullScreenChange(): void {
    const fullscreenElement: Element = document.fullscreenElement;

    if (!BaseUtils.isObjectDefined(fullscreenElement)) {
      const footer: HTMLElement = HTMLUtils.get(".footer");
      let videosWrapList: NodeListOf<HTMLElement> = HTMLUtils.list(".video-columns-wrap, .videos-wrap");
      footer.classList.remove("hidden");
      videosWrapList.item(0).classList.remove("js-fullscreen");
      videosWrapList.item(1).classList.remove("js-fullscreen");
    }
  }

  private toggleFullScreen(): void {
    const footer: HTMLElement = HTMLUtils.get(".footer");
    let videosWrapList: NodeListOf<HTMLElement> = HTMLUtils.list(".video-columns-wrap, .videos-wrap");
    if (Util.isFullScreen()) {
      Util.trace('Exiting fullscreen.');
      footer.classList.remove("hidden");
      videosWrapList.item(0).classList.remove("js-fullscreen");
      videosWrapList.item(1).classList.remove("js-fullscreen");
      Util.cancelFullScreen();
    } else {
      Util.trace('Entering fullscreen.');
      footer.classList.add("hidden");
      videosWrapList.item(0).classList.add("js-fullscreen");
      videosWrapList.item(1).classList.add("js-fullscreen");
      Util.requestFullscreen();
    }
    if (this.fullscreenIconSet)
      this.fullscreenIconSet.toggle();
  };

  private iconEventSetup(): void {
    this.icons.addEventListener("onmouseenter",
      (event: MouseEvent): void => { window.clearTimeout(this.hideIconsAfterTimeout); });
    this.icons.addEventListener("mouseleave", (event: MouseEvent): void => { this.setIconTimeout(); });
  };

  private setIconTimeout(): void {
    if (this.hideIconsAfterTimeout) {
      window.clearTimeout.bind(this, this.hideIconsAfterTimeout);
    }
    this.hideIconsAfterTimeout = window.setTimeout((): void => {
      this.hideIcons();
    }, 5000);
  };

  private onCloseOverlayClick(): void {
    this.hide(this.overlayWaitingDiv);
  };

  private onTextChatButtonClick(): void {
    this.show(this.textChatWindow);
  };

  private onTextChatCloseButtonClick(): void {
    this.hide(this.textChatWindow);
  };

  private onTextChatCollapseButtonClick(): void {
    this.hide(this.textChatWindow);
  };

  private onTextChatInputKeyUp(event: KeyboardEvent): void {
    var message = this.textChatInput.value.trim().replace(/(\r\n|\n|\r)/gm, "");
    if (event.keyCode == 13 && message.length > 0) {
      this.addLocalMessageHTML(message);
      this.sendLocalChatMessage(message);
      this.textChatInput.value = "";
    }
  };

  private onRemoteTextChatMessage(message: string): void {
    this.show(this.textChatWindow);
    this.addRemoteMessageHTML(message);
  }

  private addRemoteMessageHTML(message: string): void {
    const messageHTML: string = HTMLTemplates.getRemoteMessageHTML(message);
    this.textChatMessageList.insertAdjacentHTML('beforeend', messageHTML);
    this.textChatMessageList.scrollTop = this.textChatMessageList.scrollHeight -
      this.textChatMessageList.clientHeight;
  };

  private addLocalMessageHTML(message: string): void {
    const messageHTML = HTMLTemplates.getLocalMessageHTML(message);
    this.textChatMessageList.insertAdjacentHTML('beforeend', messageHTML);
    this.textChatMessageList.scrollTop = this.textChatMessageList.scrollHeight -
      this.textChatMessageList.clientHeight;
  };

  private sendLocalChatMessage(message: string): void {
    var messageObj: any = {};
    messageObj.type = "text-chat-message";
    messageObj.id = "text-chat-message";
    messageObj.body = message;
    this.call.send(messageObj);
  }

  private transitionToDone(): void {
    // Stop waiting for remote video.
    this.remoteVideo.oncanplay = undefined;
    this.deactivate(this.localVideo);
    this.deactivate(this.remoteVideo);
    this.deactivate(this.miniVideo);
    this.hide(this.hangupSvg);
    this.hide(this.textChatButton);
    this.hide(this.textChatWindow);
    this.activate(this.rejoinDiv);
    this.show(this.rejoinDiv);
    this.displayStatus(StringUtils.EMPTY);
  };

  private displayStatus(status: string): void {
    if (StringUtils.isEmpty(status)) {
      this.deactivate(this.statusDiv);
    } else {
      this.activate(this.statusDiv);
    }
    this.statusDiv.innerHTML = status;
  };

  private displayError(error: string): void {
    Util.warn(error);
    this.infoBox.pushErrorMessage(error);
  };

  private displaySharingInfo(data: any): void {
    const roomID: string = data.roomID;
    let roomLink: string = data.roomLink;

    this.roomLink = roomLink = window.location.protocol + "//" + window.location.host + "/r/" + roomID;

    this.pushCallNavigation(roomID, roomLink);

    var isRoomAThirdPartyAccesedRoom = /[a-zA-Z0-9]+\-\d+/g.test(roomID);
    this.activate(this.sharingDiv);

    if (!isRoomAThirdPartyAccesedRoom)
      this.activate(this.overlayWaitingDiv);
  };

  protected pushCallNavigation(roomID: string, roomLink: string): void {
    this.updateInvitationLinks(roomID);
    window.history.pushState({ 'roomId': roomID, 'roomLink': roomLink }, roomID,
      roomLink);
  };

  private updateInvitationLinks(roomID: String): void {
    let tmpRoomLink: string = "https://www.quickhellou.com/r/" + roomID;

    let invitationLink: HTMLLinkElement = HTMLUtils.get(`.overlay--send-invitation__message-editable a`) as HTMLLinkElement;

    this.roomLinkHref.href = invitationLink.href = tmpRoomLink;
    this.roomLinkHref.textContent = invitationLink.innerHTML = tmpRoomLink;
    this.roomLinkHrefWaiting.textContent = tmpRoomLink;
    this.roomLinkHrefWaiting.href = tmpRoomLink;

    let inviteBySMSButtonElement: HTMLElement = HTMLUtils.get(".invite-waiting-button--sms");
    if (Util.isMobile()) {
      inviteBySMSButtonElement.addEventListener("click", (event: MouseEvent): void => {
        window.open('sms://?body=Please join talk with me:' + tmpRoomLink, '_self');
      });
    } else inviteBySMSButtonElement.style.display = "none";

  };

  onRemoteStreamAdded(stream: MediaStream): void {
    this.deactivate(this.sharingDiv);
    this.deactivate(this.overlayWaitingDiv);
    Util.trace('Remote stream added.');
    this.remoteVideo.srcObject = stream;

    this.infoBox.getRemoteTrackIds(stream);

    if (this.remoteVideoResetTimer) {
      clearTimeout(this.remoteVideoResetTimer);
      this.remoteVideoResetTimer = null;
    }
  };

  private onLocalStreamAdded(stream: MediaStream): void {
    Util.trace('User has granted access to local media.');
    this.localStream = stream;
    this.infoBox.getLocalTrackIds(this.localStream);

    if (!this.roomSelection) {
      this.attachLocalStream();
    }
  };

  private onRemoteSdpSet(hasRemoteVideo: boolean): void {
    if (hasRemoteVideo) {
      Util.trace('Waiting for remote video.');
      this.waitForRemoteVideo();
    } else {
      Util.trace('No remote video stream; not waiting for media to arrive.');
      // TODO(juberti): Make this wait for ICE connection before transitioning.
      this.transitionToActive();
    }
  };

  private waitForRemoteVideo(): any {
    // Wait for the actual video to start arriving before moving to the active
    // call state.
    if (this.remoteVideo.readyState >= 2) { // i.e. can play
      Util.trace('Remote video started; currentTime: ' +
        this.remoteVideo.currentTime);
      this.transitionToActive();
    } else {
      this.remoteVideo.oncanplay = (): void => { this.waitForRemoteVideo() };
    }
  };

  private transitionToActive(): void {
    // Stop waiting for remote video.
    this.remoteVideo.oncanplay = undefined;
    var connectTime = window.performance.now();
    this.infoBox.setSetupTimes(this.call.getStartTime(), connectTime);
    this.infoBox.updateInfoDiv();
    Util.trace('Call setup time: ' + (connectTime - this.call.getStartTime()).toFixed(0) +
      'ms.');

    // Prepare the remote video and PIP elements.
    Util.trace('reattachMediaStream: ' + this.localVideo.srcObject);
    this.miniVideo.srcObject = this.localVideo.srcObject;

    // Transition opacity from 0 to 1 for the remote and mini videos.
    this.activate(this.remoteVideo);
    this.activate(this.miniVideo);
    // Transition opacity from 1 to 0 for the local video.
    this.deactivate(this.localVideo);
    this.localVideo.srcObject = null;
    // Rotate the div containing the videos 180 deg with a CSS transform.
    this.activate(this.videosDiv);

    let videosWrapElement: HTMLElement = HTMLUtils.get(".videos-wrap");
    videosWrapElement.classList.add("active");

    this.show(this.hangupSvg);
    this.show(this.textChatButton);
    this.displayStatus('');
  };

  private transitionToWaiting(): void {
    // Stop waiting for remote video.
    this.remoteVideo.oncanplay = undefined;

    this.hide(this.hangupSvg);
    this.hide(this.textChatButton);
    this.hide(this.textChatWindow);

    // Rotate the div containing the videos -180 deg with a CSS transform.
    this.deactivate(this.videosDiv);

    if (!this.remoteVideoResetTimer) {
      this.remoteVideoResetTimer = setTimeout((): void => {
        this.remoteVideoResetTimer = null;
        Util.trace('Resetting remoteVideo src after transitioning to waiting.');
        this.remoteVideo.srcObject = null;
      }, 800);
    }

    // Set localVideo.srcObject now so that the local stream won't be lost if the
    // call is restarted before the timeout.
    this.localVideo.srcObject = this.miniVideo.srcObject;

    // Transition opacity from 0 to 1 for the local video.
    this.activate(this.localVideo);
    // Transition opacity from 1 to 0 for the remote and mini videos.
    this.deactivate(this.remoteVideo);
    this.deactivate(this.miniVideo);
  }

  private onWssResponse(data: any): void {
    const message: string = data.message;
    Util.trace("onWssResponse:" + message);
  }

  private onRemoteHangup = (): void => {
    this.displayStatus('The remote side hung up.');
    this.transitionToWaiting();

    // reduntant call
    this.call.onRemoteHangup();
  };

  private showIcons(): void {
    if (!this.icons.classList.contains('active')) {
      this.activate(this.fullscreenIcon);
      this.activate(this.icons);
      this.setIconTimeout();
    }
  };

  private hideIcons(): void {
    if (this.icons.classList.contains('active')) {
      this.deactivate(this.icons);
      this.deactivate(this.fullscreenIcon);
    }
  };

  private hide = (element: HTMLElement): void => {
    element.classList.add('hidden');
  };

  private show = (element: HTMLElement): void => {
    element.classList.remove('hidden');
  };

  private activate = (element: HTMLElement): void => {
    element.classList.add('active');
  };

  private deactivate = (element: HTMLElement): void => {
    element.classList.remove('active');
  };

  private callConsole() {
    //const url = this.loadingParams.wssUrl;
    const url = "ws://127.0.0.1:8089/ws";
    Util.trace(`Calling the console application using URL: ${url}`);
    
    const websocket = new WebSocket(url);
    websocket.onopen = (): void => {
      Util.trace('Signaling channel opened for console communication.');
      var wssMessage = {
        cmd: 'console',
        msg: 'hello'
      };
      var msgString = JSON.stringify(wssMessage);
      if (websocket && websocket.readyState === WebSocket.OPEN) {
        Util.trace('Message to console sent.');
        websocket.send(msgString);
      } else {
        Util.trace('Message to console sending failure. No websocket connection');
      }
    }    
  } 
}