import { EventDispatcherService } from "../../../../web_plugin/ts/com/genb/base/services/EventDispatcherService";
import { BaseUtils } from "../../../../web_plugin/ts/com/genb/base/utils/BaseUtils";
import { Util } from "./Util";
import { SignalingChannelEvent } from "./SignalingChannelEvent";
import { StringUtils } from "../../../../web_plugin/ts/com/genb/base/utils/StringUtils";

export class SignalingChannel extends EventDispatcherService {
  private wssUrl: string;
  private wssPostUrl: string;

  private roomID: string;
  private clientID: string;

  public websocket: WebSocket;
  private registered: boolean = false;

  private ROOM_TYPE: string = "room";

  public onerror: Function;

  public static MESSAGE: string = "message";

  constructor(wssUrl: string, wssPostUrl: string) {
    super();
    this.wssUrl = wssUrl;
    this.wssPostUrl = wssPostUrl;
  }

  open():Promise<any> {
    if (BaseUtils.isObjectDefined(this.websocket)) {
      Util.trace('ERROR: SignalingChannel has already opened.');
      return;
    }

    Util.trace(`Opening signaling channel: ${this.wssUrl}`);
    return new Promise((resolve: any, reject: any) => {
      this.websocket = new WebSocket(this.wssUrl);

      this.websocket.onopen = (): void => {
        Util.trace('Signaling channel opened.');

        this.websocket.onerror = (): void => {
          reject('Signaling channel error.');
        };
        this.websocket.onclose = (event: any): void => {
          Util.trace('Channel closed with code:' + event.code +
            ' reason:' + event.reason);
          this.websocket = null;
          this.registered = false;
          this.dispatchEvent(SignalingChannelEvent.CLOSE, event.code);
        };

        if (BaseUtils.isObjectDefined(this.clientID) && BaseUtils.isObjectDefined(this.roomID)) {
          this.register(this.roomID, this.clientID);
        }

        resolve();
      }

      this.websocket.onmessage = (event: any) => {
        Util.trace('WSS->C: ' + event.data);

        var message: any = Util.parseJSON(event.data);
        if (!BaseUtils.isObjectDefined(message)) {
          Util.trace('Failed to parse WSS message: ' + event.data);
          return;
        }
        if (!StringUtils.isEmpty(message.error)) {
          Util.trace('Signaling server error message: ' + message.error);
          return;
        }
        this.dispatchEvent(SignalingChannelEvent.MESSAGE, message.msg);
      };

      this.websocket.onerror = (): void => {
        reject(Error('WebSocket error.'));
      };
    });
  }

  register(roomID: string, clientID: string) {
    return new Promise((resolve, reject) => {
      this.roomID = roomID;
      this.clientID = clientID;
      if (!BaseUtils.isObjectDefined(this.roomID)) {
        Util.trace('ERROR: missing roomID.');
      }
      if (!BaseUtils.isObjectDefined(this.clientID)) {
        Util.trace('ERROR: missing clientID.');
      }
      if (!this.websocket || this.websocket.readyState !== WebSocket.OPEN) {
        return reject({ code: 0, message: 'WebSocket not open.' });
      }
      Util.trace('Registering signaling channel.');
      var registerMessage = {
        cmd: 'register',
        roomid: this.roomID,
        roomtype: this.ROOM_TYPE,
        clientid: this.clientID
      };
      this.websocket.send(JSON.stringify(registerMessage));
      this.registered = true;

      Util.trace('Signaling channel registered.');
      return resolve({ code: 1, message: 'Signaling channel registered.' });
    });
  }

  close(async: boolean) {
    if (this.websocket) {
      this.websocket.close();
      this.websocket = null;
    }

    if (!this.clientID || !this.roomID) {
      return;
    }
    // Tell WSS that we're done.
    var path = this.getWssPostUrl();
    return Util.sendUrlRequest('DELETE', path, async).catch((error:any) => {
        console.log('Error deleting web socket connection: ' + error.message);
      }).then( ():void => {
        this.clientID = null;
        this.roomID = null;
        this.registered = false;
    });
  }

  /**
   * Sends a websocket message.
   *
   * @param {string} message
   * @returns
   * @memberof SignalingChannel
   */
  public send(message:any) {
    if (!this.roomID || !this.clientID) {
        Util.trace('ERROR: SignalingChannel has not registered.');
        return;
      }

      var wssMessage = {
        cmd: 'send',
        msg: message
      };
      this.postMessage(wssMessage);
  }

  postMessage(wssMessage:any):void {
    var msgString = JSON.stringify(wssMessage);
    Util.trace('C->WSS: ' + msgString);
    if (this.websocket && this.websocket.readyState === WebSocket.OPEN) {
      this.websocket.send(msgString);
    } else {
      var path = this.getWssPostUrl();
      var xhr = new XMLHttpRequest();
      xhr.open('POST', path, true);
      xhr.send(wssMessage.msg);
    }
  };

  public getWssPostUrl():string {
    return this.wssPostUrl + '/' + this.roomID + '/' + this.clientID;
  };
}