import { LoadingParams } from "./LoadingParams";
import { Util } from "./Util";
import { SDPUtils } from "./SDPUtils";
import { EventDispatcherService } from "../genb/base/services/EventDispatcherService";
import { PeerConnectionClientEvent } from "./PeerConnectionClientEvent";
import { BaseUtils } from "../genb/base/utils/BaseUtils";
import { Constants } from "./Constants";
import { StringUtils } from "../genb/base/utils/StringUtils";

export class PeerConnectionClient extends EventDispatcherService {
  private params: LoadingParams;
  private startTime: number;
  private pc: RTCPeerConnection;

  private messageQueue: Array<string>;

  private hasRemoteSdp: boolean;
  private isInitiator: boolean;
  private started: boolean;

  private static DEFAULT_SDP_OFFER_OPTIONS: object = {
    offerToReceiveAudio: 1,
    offerToReceiveVideo: 1,
    voiceActivityDetection: false
  };

  constructor(params: LoadingParams, startTime: number) {
    super();
    this.params = params;
    this.startTime = startTime;

    Util.trace('Creating RTCPeerConnnection with:\n' +
      '  config: \'' + JSON.stringify(params.peerConnectionConfig) + '\';\n' +
      '  constraints: \'' + JSON.stringify(params.peerConnectionConstraints) +
      '\'.');

    // Create an RTCPeerConnection via the polyfill (adapter.js).
    this.pc = new (RTCPeerConnection as any)(params.peerConnectionConfig, params.peerConnectionConstraints);
    this.pc.onicecandidate = (event: RTCPeerConnectionIceEvent ): void => { this.onIceCandidate(event); };
    this.pc.ontrack = (event: RTCTrackEvent): void => { this.onRemoteStreamAdded(event); };
    
    
    /*
    RTCPeerConnection.onremovestream
    
    The removestream event has been removed from the WebRTC specification in favor of the existing removetrack event on the remote MediaStream 
    and the corresponding MediaStream.onremovetrack event handler property of the remote MediaStream. 
    */
    //this.pc.onremovestream = (event: MediaStreamEvent): void => { Util.trace('Remote stream removed.'); };
    
    this.pc.onsignalingstatechange = (event: Event): void => { this.onSignalingStateChanged(); };
    this.pc.oniceconnectionstatechange = (event: Event): void => { this.onIceConnectionStateChanged(); };
    this.pc.onRemoteTextChatMessage = (data: any) => { this.onRemoteTextChatMessage(data); };
    this.pc.onWssResponse = (data: any) => { this.onWssResponse(data); };
    window.dispatchEvent(new CustomEvent('pccreated', {
      detail: {
        pc: this,
        time: new Date(),
        userId: this.params.roomID + (this.isInitiator ? '-0' : '-1'),
        sessionId: this.params.roomID
      }
    }));

    this.hasRemoteSdp = false;
    this.messageQueue = new Array<string>();
    this.isInitiator = false;
    this.started = false;

  }

  public receiveSignalingMessage(message: any): void {
    var messageObj: any = Util.parseJSON(message);
    if (!BaseUtils.isObjectDefined(messageObj)) {
      return;
    }
    if ((this.isInitiator && messageObj.type === 'answer') ||
      (!this.isInitiator && messageObj.type === 'offer')) {
      this.hasRemoteSdp = true;
      // Always process offer before candidates.
      this.messageQueue.unshift(messageObj);
    } else if (messageObj.type === 'candidate') {
      this.messageQueue.push(messageObj);
    } else if (messageObj.type === 'text-chat-message') {
      this.dispatchEvent(PeerConnectionClientEvent.REMOTE_TEXT_CHAT_MESSAGE, { "message": messageObj.body });
    } else if (messageObj.type === 'bye') {
      this.dispatchEvent(PeerConnectionClientEvent.REMOTE_HANGUP);
    }
    this.drainMessageQueue();
  };

  private setRemoteSdp(message: any): void {
    message.sdp = SDPUtils.maybeSetOpusOptions(message.sdp, this.params);
    message.sdp = SDPUtils.maybePreferAudioSendCodec(message.sdp, this.params);
    message.sdp = SDPUtils.maybePreferVideoSendCodec(message.sdp, this.params);
    message.sdp = SDPUtils.maybeSetAudioSendBitRate(message.sdp, this.params);
    message.sdp = SDPUtils.maybeSetVideoSendBitRate(message.sdp, this.params);
    message.sdp = SDPUtils.maybeSetVideoSendInitialBitRate(message.sdp, this.params);
    message.sdp = SDPUtils.maybeRemoveVideoFec(message.sdp, this.params);
    this.pc.setRemoteDescription(new RTCSessionDescription(message))
      .then((): void => { this.onSetRemoteDescriptionSuccess(); })
      .catch((): void => { this.dispatchEvent(PeerConnectionClientEvent.ERROR, 'setRemoteDescription'); });
  };

  private onSetRemoteDescriptionSuccess(): void {
    Util.trace('Set remote session description success.');
    // By now all onaddstream events for the setRemoteDescription have fired,
    // so we can know if the peer has any remote video streams that we need
    // to wait for. Otherwise, transition immediately to the active state.
    var remoteStreams: MediaStream[] = this.getRemoteStreams();


    const hasRemoteVideo:boolean = remoteStreams.length > 0 && remoteStreams[0].getVideoTracks().length > 0;
    this.dispatchEvent(PeerConnectionClientEvent.REMOTE_SDP_SET,
      {"hasRemoteVideo":hasRemoteVideo});
  };

  private processSignalingMessage(message: any): void {
    if (message.type === 'offer' && !this.isInitiator) {
      if (this.pc.signalingState !== 'stable') {
        Util.warn('Remote offer received in unexpected state: ' +
          this.pc.signalingState);
        return;
      }
      this.setRemoteSdp(message);
      this.doAnswer();
    } else if (message.type === 'answer' && this.isInitiator) {
      if (this.pc.signalingState !== 'have-local-offer') {
        Util.warn('Remote answer received in unexpected state: ' +
          this.pc.signalingState);
        return;
      }
      this.setRemoteSdp(message);
    } else if (message.type === 'candidate') {
      var candidate = new RTCIceCandidate({
        sdpMLineIndex: message.label,
        candidate: message.candidate
      });
      this.recordIceCandidate('Remote', candidate);
      this.pc.addIceCandidate(candidate)
        .then((): void => { Util.trace('Remote candidate added successfully.'); })
        .catch((reason:string): void => { this.dispatchEvent(PeerConnectionClientEvent.ERROR, `ERROR:addIceCandidate => ${reason}`); });
    } else {
      Util.trace('WARNING: unexpected message: ' + JSON.stringify(message));
    }
  };

  // When we receive messages from GAE registration and from the WSS connection,
  // we add them to a queue and drain it if conditions are right.
  private drainMessageQueue(): void {
    // It's possible that we finish registering and receiving messages from WSS
    // before our peer connection is created or started. We need to wait for the
    // peer connection to be created and started before processing messages.
    //
    // Also, the order of messages is in general not the same as the POST order
    // from the other client because the POSTs are async and the server may handle
    // some requests faster than others. We need to process offer before
    // candidates so we wait for the offer to arrive first if we're answering.
    // Offers are added to the front of the queue.
    if (!this.pc || !this.started || !this.hasRemoteSdp) {
      return;
    }
    for (var i: number = 0, len = this.messageQueue.length; i < len; i++) {
      this.processSignalingMessage(this.messageQueue[i]);
    }
    this.messageQueue = [];
  };

  private doAnswer(): void {
    Util.trace('Sending answer to peer.');
    this.pc.createAnswer()
      .then((sessionDescription:any):void => {this.setLocalSdpAndNotify(sessionDescription);})
      .catch((reason:String): void => { this.dispatchEvent(PeerConnectionClientEvent.ERROR, `ERROR:createAnswer => ${reason}`); });
  };

  private setLocalSdpAndNotify(sessionDescription: any): void {
    sessionDescription.sdp = SDPUtils.maybePreferAudioReceiveCodec(
      sessionDescription.sdp,
      this.params);
    sessionDescription.sdp = SDPUtils.maybePreferVideoReceiveCodec(
      sessionDescription.sdp,
      this.params);
    sessionDescription.sdp = SDPUtils.maybeSetAudioReceiveBitRate(
      sessionDescription.sdp,
      this.params);
    sessionDescription.sdp = SDPUtils.maybeSetVideoReceiveBitRate(
      sessionDescription.sdp,
      this.params);
    sessionDescription.sdp = SDPUtils.maybeRemoveVideoFec(
      sessionDescription.sdp,
      this.params);
    this.pc.setLocalDescription(sessionDescription)
      .then((): void => { Util.trace('Set session description success.'); })
      .catch((reason: string): void => {
        this.dispatchEvent(PeerConnectionClientEvent.ERROR, `ERROR::setLocalDescription => ${reason}`); });

    // Chrome version of RTCSessionDescription can't be serialized directly
    // because it JSON.stringify won't include attributes which are on the
    // object's prototype chain. By creating the message to serialize
    // explicitly we can avoid the issue.
    this.dispatchEvent(PeerConnectionClientEvent.SIGNALING_MESSAGE, {
      "sdp": sessionDescription.sdp,
      "type": sessionDescription.type
    })
  };

  private recordIceCandidate(location: string, candidate: RTCIceCandidate): void {
    this.dispatchEvent(PeerConnectionClientEvent.NEW_ICE_CANDIDATE, 
      { "location": location, "candidate": candidate.candidate })
  };

  public getPeerConnectionStats(callback: any): void {
    if (!BaseUtils.isObjectDefined(this.pc)) {
      return;
    }

    this.pc.getStats(null)
      .then(callback);
  };

  public addStream(stream: MediaStream): void {
    if (!BaseUtils.isObjectDefined(this.pc)) {
      return null;
    }
    
    //this.pc.addStream(stream);
    stream.getTracks().forEach((track) => {
      this.pc.addTrack(track, stream);
    });

  };

  public getPeerConnectionStates(): any {
    if (!BaseUtils.isObjectDefined(this.pc)) {
      return null;
    }

    return {
      'signalingState': this.pc.signalingState,
      'iceGatheringState': this.pc.iceGatheringState,
      'iceConnectionState': this.pc.iceConnectionState
    };
  };

  public close():void {
    if (!BaseUtils.isObjectDefined(this.pc)) {
      return;
    }

    this.pc.close();
    window.dispatchEvent(new CustomEvent('pcclosed', {
      detail: {
        pc: this,
        time: new Date(),
      }
    }));
    this.pc = null;
  }

  public startAsCaller(offerOptions: any): boolean {
    if (!BaseUtils.isObjectDefined(this.pc)) {
      return false;
    }

    if (this.started) {
      return false;
    }

    this.isInitiator = true;
    this.started = true;
    let constraints: any = SDPUtils.mergeConstraints(
      PeerConnectionClient.DEFAULT_SDP_OFFER_OPTIONS, offerOptions);
    Util.trace('Sending offer to peer, with constraints: \n\'' +
      JSON.stringify(constraints) + '\'.');
    this.pc.createOffer(constraints)
      .then((sessionDescription: any): any => { this.setLocalSdpAndNotify(sessionDescription); })
      .catch((reason: string): void => this.dispatchEvent(PeerConnectionClientEvent.ERROR, `ERROR:createOffer => ${reason}`));

    return true;
  };

  public startAsCallee(initialMessages: any): boolean {
    if (!BaseUtils.isObjectDefined(this.pc)) {
      return false;
    }

    if (this.started) {
      return false;
    }

    this.isInitiator = false;
    this.started = true;

    if (initialMessages && initialMessages.length > 0) {
      // Convert received messages to JSON objects and add them to the message
      // queue.
      for (var i = 0, len = initialMessages.length; i < len; i++) {
        this.receiveSignalingMessage(initialMessages[i]);
      }
      return true;
    }

    // We may have queued messages received from the signaling channel before
    // started.
    if (this.messageQueue.length > 0) {
      this.drainMessageQueue();
    }
    return true;
  };

  private onSignalingStateChanged(): void {
    if (!BaseUtils.isObjectDefined(this.pc)) {
      return;
    }

    Util.trace('Signaling state changed to: ' + this.pc.signalingState);

    this.dispatchEvent(PeerConnectionClientEvent.SIGNALING_STATE_CHANGE);
  };

  private onIceConnectionStateChanged(): void {
    if (!BaseUtils.isObjectDefined(this.pc)) {
      return;
    }

    Util.trace('ICE connection state changed to: ' + this.pc.iceConnectionState);
    if (this.pc.iceConnectionState === 'completed') {
      Util.trace('ICE complete time: ' +
        (window.performance.now() - this.startTime).toFixed(0) + 'ms.');
    }

    this.dispatchEvent(PeerConnectionClientEvent.ICE_CONNECTION_STATE_CHANGE);
  };

  private onIceCandidate(event: RTCPeerConnectionIceEvent): void {
    if (event.candidate) {
      // Eat undesired candidates.
      if (this.filterIceCandidate(event.candidate)) {
        var message: object = {
          type: 'candidate',
          label: event.candidate.sdpMLineIndex,
          id: event.candidate.sdpMid,
          candidate: event.candidate.candidate
        };
        this.dispatchEvent(PeerConnectionClientEvent.SIGNALING_MESSAGE, message);
        this.recordIceCandidate('Local', event.candidate);
      }
    } else {
      Util.trace('End of candidates.');
    }
  };

  /**
   * Return false if the candidate should be dropped, true if not. 
   * 
   * @private
   * @param {RTCIceCandidate} candidate 
   * @returns {boolean} 
   * @memberof PeerConnectionClient
   */
  private filterIceCandidate(candidate: RTCIceCandidate): boolean {
    let candidateStr: string = candidate.candidate;

    // Always eat TCP candidates. Not needed in this context.
    if (candidateStr.indexOf(Constants.TCP) !== -1) {
      return false;
    }

    // If we're trying to eat non-relay candidates, do that.
    if (StringUtils.equals(this.params.peerConnectionConfig.iceTransports,Constants.RELAY) &&
      !StringUtils.equals(SDPUtils.iceCandidateType(candidateStr),Constants.RELAY)) {
      return false;
    }

    return true;
  };

  private onRemoteStreamAdded(event: any): void {
    this.dispatchEvent(PeerConnectionClientEvent.REMOTE_STREAM_ADDED, { "stream": event.streams[0] });
  };

  private onWssResponse(data: any): void {
    this.dispatchEvent(PeerConnectionClientEvent.WSS_RESPONSE, { "message": data });
  }

  private onRemoteTextChatMessage(message: any): void {
    this.dispatchEvent(PeerConnectionClientEvent.REMOTE_TEXT_CHAT_MESSAGE, { "message": message });
  }

  private getRemoteStreams () {
    var stream = new MediaStream();
    this.pc.getReceivers().forEach(function (receiver:RTCRtpReceiver) {
        stream.addTrack(receiver.track);
    });
    return [stream];
  };

}