import { SignalingChannel } from "./SignalingChannel";
import { LoadingParams } from "./LoadingParams";
import { PeerConnectionClient } from "./PeerConnectionClient";
import { EventDispatcherService } from "../genb/base/services/EventDispatcherService";
import { CallEvent } from "./CallEvent";
import { Util } from "./Util";
import { SignalingChannelEvent } from "./SignalingChannelEvent";
import { PeerConnectionClientEvent } from "./PeerConnectionClientEvent";
import { BaseUtils } from "../genb/base/utils/BaseUtils";
import { Constants } from "./Constants";
import { StringUtils } from "../genb/base/utils/StringUtils";

export class Call extends EventDispatcherService {
  private channel: SignalingChannel;
  private params: LoadingParams;
  private roomServer: string;
  private pcClient: PeerConnectionClient;
  private localStream: MediaStream;
  private errorMessageQueue: Array<any>;
  private startTime: number;

  private getMediaPromise: Promise<any>;
  private getIceServersPromise: Promise<any>;

  constructor(params: LoadingParams) {
    super();
    this.params = params;
    this.roomServer =
      window.location.protocol + "//" + window.location.host || "";
    this.channel = new SignalingChannel(params.wssUrl, params.wssPostUrl);
    this.channel.addEventListener(
      SignalingChannelEvent.MESSAGE,
      this.onRecvSignalingChannelMessage,
      this
    );

    this.pcClient = null;
    this.localStream = null;
    this.errorMessageQueue = new Array<any>();
    this.startTime = null;

    this.getMediaPromise = null;
    this.getIceServersPromise = null;

    this.requestMediaAndIceServers();
  }

  public send(message: any): void {
    var msgString = JSON.stringify(message);
    this.channel.send(msgString);
  }

  private requestMediaAndIceServers(): void {
    this.getMediaPromise = this.maybeGetMedia();
    this.getIceServersPromise = this.maybeGetIceServers();
  }

  // Asynchronously request user media if needed.
  private maybeGetMedia(): Promise<any> {
    // mediaConstraints.audio and mediaConstraints.video could be objects, so
    // check '!=== false' instead of '=== true'.
    let needStream: boolean =
      this.params.mediaConstraints.audio !== false ||
      this.params.mediaConstraints.video !== false;
    var mediaPromise: Promise<any> = null;
    if (needStream) {
      let mediaConstraints: any = this.params.mediaConstraints;

      mediaPromise = navigator.mediaDevices
        .getUserMedia(mediaConstraints)
        .catch((error: any): any => {
          if (error.name !== "NotFoundError") {
            throw error;
          }
          return navigator.mediaDevices
            .enumerateDevices()
            .then((devices: any): any => {
              var cam = devices.find((device: any): boolean => {
                return device.kind === "videoinput";
              });
              var mic = devices.find((device: any): boolean => {
                return device.kind === "audioinput";
              });
              var constraints = {
                video: cam && mediaConstraints.video,
                audio: mic && mediaConstraints.audio
              };
              return navigator.mediaDevices.getUserMedia(constraints);
            });
        })
        .then((stream: MediaStream): any => {
          Util.trace(
            "Got access to local media with mediaConstraints:\n" +
              "  '" +
              JSON.stringify(mediaConstraints) +
              "'"
          );

          this.onUserMediaSuccess(stream);
        })
        .catch((error: any): void => {
          this.onError("Error getting user media: " + error.message);
          this.onUserMediaError(error);
        });
    } else {
      mediaPromise = Promise.resolve();
    }
    return mediaPromise;
  }

  // Asynchronously request an ICE server if needed.
  private maybeGetIceServers(): Promise<any> {
    var shouldRequestIceServers =
      this.params.iceServerRequestUrl &&
      this.params.iceServerRequestUrl.length > 0 &&
      this.params.peerConnectionConfig.iceServers &&
      this.params.peerConnectionConfig.iceServers.length === 0;

    let iceServerPromise: Promise<any> = null;
    if (shouldRequestIceServers) {
      const requestURL: string = this.params.iceServerRequestUrl;
      iceServerPromise = Util.requestIceServers(
        requestURL,
        this.params.iceServerTransports
      )
        .then((iceServers: any): void => {
          var servers = this.params.peerConnectionConfig.iceServers;
          this.params.peerConnectionConfig.iceServers = servers.concat(
            iceServers
          );
        })
        .catch((error: any): void => {
          // Error retrieving ICE servers.
          const message: string = `No TURN server; unlikely that media will traverse networks.`;
          this.dispatchEvent(CallEvent.STATUS_MESSAGE, message);
          Util.warn(error.message);
        });
    } else {
      iceServerPromise = Promise.resolve();
    }
    return iceServerPromise;
  }

  private onRecvSignalingChannelMessage(msg: string): void {
    this.maybeCreatePcClientAsync().then((): void => {
      this.pcClient.receiveSignalingMessage(msg);
    });
  }

  private maybeCreatePcClientAsync = (): Promise<any> => {
    return new Promise((resolve: Function, reject: Function): void => {
      if (this.pcClient) {
        resolve();
        return;
      }

      if (
        typeof (RTCPeerConnection as any).generateCertificate === "function"
      ) {
        var certParams = { name: "ECDSA", namedCurve: "P-256" };
        (RTCPeerConnection as any)
          .generateCertificate(certParams)
          .then((cert: string): void => {
            Util.trace("ECDSA certificate generated successfully.");
            this.params.peerConnectionConfig.certificates = [cert];
            this.createPcClient();
            resolve();
          })
          .catch((error: string): void => {
            Util.trace("ECDSA certificate generation failed.");
            reject(error);
          });
      } else {
        this.createPcClient();
        resolve();
      }
    });
  };

  /**
   * Creates new peer connection client.
   *
   * @private
   * @memberof Call
   */
  private createPcClient() {
    this.pcClient = new PeerConnectionClient(this.params, this.startTime);
    this.pcClient.addEventListener(
      PeerConnectionClientEvent.SIGNALING_MESSAGE,
      (data: any) => {
        this.sendSignalingMessage(data);
      },
      this
    );
    this.pcClient.addEventListener(
      PeerConnectionClientEvent.REMOTE_HANGUP,
      (data: any) => {
        this.dispatchEvent(CallEvent.REMOTE_HANGUP);
      },
      this
    );
    this.pcClient.addEventListener(
      PeerConnectionClientEvent.REMOTE_SDP_SET,
      (data: any) => {
        this.dispatchEvent(CallEvent.REMOTE_SDP_SET, data);
      },
      this
    );
    this.pcClient.addEventListener(
      PeerConnectionClientEvent.REMOTE_STREAM_ADDED,
      (data: any) => {
        this.dispatchEvent(CallEvent.REMOTE_STREAM_ADDED, data);
      },
      this
    );
    this.pcClient.addEventListener(
      PeerConnectionClientEvent.SIGNALING_STATE_CHANGE,
      (): void => {
        this.dispatchEvent(CallEvent.SIGNALING_STATE_CHANGE);
      },
      this
    );
    this.pcClient.addEventListener(
      PeerConnectionClientEvent.ICE_CONNECTION_STATE_CHANGE,
      (): void => {
        this.dispatchEvent(CallEvent.ICE_CONNECTION_STATE_CHANGE);
      },
      this
    );
    this.pcClient.addEventListener(
      PeerConnectionClientEvent.NEW_ICE_CANDIDATE,
      (data: any): void => {
        this.dispatchEvent(CallEvent.NEW_ICE_CANDIDATE, data);
      },
      this
    );
    this.pcClient.addEventListener(
      PeerConnectionClientEvent.ERROR,
      (message: string) => {
        this.onError(message);
      },
      this
    );
    this.pcClient.addEventListener(
      PeerConnectionClientEvent.REMOTE_TEXT_CHAT_MESSAGE,
      (data: any) => {
        this.dispatchEvent(CallEvent.REMOTE_TEXT_CHAT_MESSAGE, data);
      },
      this
    );
    this.pcClient.addEventListener(
      PeerConnectionClientEvent.WSS_RESPONSE,
      (data: any) => {
        this.dispatchEvent(CallEvent.WSS_RESPONSE, data);
      },
      this
    );
  }

  private onUserMediaSuccess(stream: MediaStream): void {
    this.localStream = stream;
    this.dispatchEvent(CallEvent.LOCAL_STREAM_ADDED, { stream: stream });
  }

  private onUserMediaError(error: any): void {
    const errorMessage: string = `Failed to get access to local media. Error name was ${error.name}. Continuing without sending a stream.`;
    this.onError("getUserMedia error: " + errorMessage);
    this.errorMessageQueue.push(error);
    alert(errorMessage);
  }

  /**
   * Reinitialize the promises so the media gets hooked up as a result
   * of calling maybeGetMedia.
   *
   * @memberof Call
   */
  public restart(): void {
    this.requestMediaAndIceServers();
    this.start(this.params.previousRoomID);
  }

  public start(roomID: string): void {
    this.connectToRoom(roomID);
    //if (this.params.isLoopback) {
    //setupLoopback(this.params.wssUrl, roomID);
    //}
  }

  private startSignaling(): void {
    Util.trace("Starting signaling.");
    if (this.isInitiator()) {
      this.dispatchEvent(CallEvent.CALLER_STARTED, {
        roomID: this.params.roomID,
        roomLink: this.params.roomLink
      });
    }

    this.startTime = window.performance.now();

    this.maybeCreatePcClientAsync()
      .then((): void => {
        if (this.localStream) {
          Util.trace("Adding local stream.");
          this.pcClient.addStream(this.localStream);
        }
        if (this.params.isInitiator) {
          this.pcClient.startAsCaller(this.params.offerOptions);
        } else {
          this.pcClient.startAsCallee(this.params.messages);
        }
      })
      .catch((e: any): void => {
        this.onError("Create PeerConnection exception: " + e);
        alert(`Cannot create RTCPeerConnection: ${e.message}`);
      });
  }

  /**
   * Connects client to the room. This happens by simultaneously requesting
   * media, requesting turn, and join the room. Once all three of those
   * tasks is complete, the signaling process begins. At the same time, a
   * WebSocket connection is opened using |wss_url| followed by a subsequent
   * registration once GAE registration completes.
   *
   * @private
   * @param {string} roomID
   * @memberof Call
   */
  private connectToRoom(roomID: string): any {
    this.params.roomID = roomID;
    // Asynchronously open a WebSocket connection to WSS.
    var channelPromise: Promise<any> = this.channel.open().catch(
      (error: any): Promise<any> => {
        this.onError("WebSocket open error: " + error.message);
        return Promise.reject(error);
      }
    );

    // Asynchronously join the room.
    var joinPromise: Promise<any> = this.joinRoom()
      .then((roomParams: any): void => {
        // The only difference in parameters should be clientId and isInitiator,
        // and the turn servers that we requested.
        this.params.clientID = roomParams.client_id;
        this.params.roomID = roomParams.room_id;
        this.params.roomLink = roomParams.room_link;
        this.params.isInitiator = roomParams.is_initiator === "true";

        this.params.messages = roomParams.messages;
      })
      .catch(
        (error: any): Promise<any> => {
          this.onError("Room server join error: " + error.message);
          return Promise.reject(error);
        }
      );

    // We only register with WSS if the web socket connection is open and if we're
    // already registered with GAE.
    Promise.all([channelPromise, joinPromise])
      .then((): void => {
        this.channel.register(this.params.roomID, this.params.clientID);

        // We only start signaling after we have registered the signaling channel
        // and have media and TURN. Since we send candidates as soon as the peer
        // connection generates them we need to wait for the signaling channel to be
        // ready.
        Promise.all([this.getIceServersPromise, this.getMediaPromise])
          .then((): void => {
            this.startSignaling();
          })
          .catch((error: any): void => {
            this.onError("Failed to start signaling: " + error.message);
          });
      })
      .catch((error: any): void => {
        this.onError("WebSocket register error: " + error.message);
      });
  }

  // Join the room and returns room parameters.
  private joinRoom(): Promise<any> {
    return new Promise((resolve: Function, reject: Function): any => {
      if (!this.params.roomID) {
        reject(Error("Missing room ID."));
      }

      let path: string = `${this.roomServer}/join/${this.params.roomID}${window.location.search}`;

      Util.sendAsyncUrlRequest("POST", path)
        .then((response: any): void => {
          var responseObj: any = Util.parseJSON(response);
          if (!responseObj) {
            reject(Error("Error parsing response JSON."));
            return;
          }
          if (responseObj.result !== "SUCCESS") {
            reject(Error("Registration error: " + responseObj.result));
            if (StringUtils.equals(responseObj.result, "FULL")) {
              var getPath =
                this.roomServer +
                "/r/" +
                this.params.roomID +
                window.location.search;
              window.location.assign(getPath);
            }
            return;
          }
          Util.trace("Joined the room.");
          resolve(responseObj.params);
        })
        .catch((error: any): void => {
          reject(Error("Failed to join the room: " + error.message));
          return;
        });
    });
  }
  public toggleVideoMute(): void {
    let videoTracks: MediaStreamTrack[] = this.localStream.getVideoTracks();
    if (videoTracks.length === 0) {
      Util.trace("No local video available.");
      return;
    }

    Util.trace("Toggling video mute state.");
    for (var i: number = 0; i < videoTracks.length; ++i) {
      videoTracks[i].enabled = !videoTracks[i].enabled;
    }
    Util.trace("Video " + (videoTracks[0].enabled ? "unmuted." : "muted."));
  }

  public toggleAudioMute(): void {
    let audioTracks: MediaStreamTrack[] = this.localStream.getAudioTracks();
    if (audioTracks.length === 0) {
      Util.trace("No local audio available.");
      return;
    }

    Util.trace("Toggling audio mute state.");
    for (var i = 0; i < audioTracks.length; ++i) {
      audioTracks[i].enabled = !audioTracks[i].enabled;
    }
    Util.trace("Audio " + (audioTracks[0].enabled ? "unmuted." : "muted."));
  }

  private sendSignalingMessage(message: any): void {
    var msgString: string = JSON.stringify(message);
    if (this.params.isInitiator) {
      // Initiator posts all messages to GAE. GAE will either store the messages
      // until the other client connects, or forward the message to Collider if
      // the other client is already connected.
      // Must append query parameters in case we've specified alternate WSS url.
      const path: string = `${this.roomServer}/message/${this.params.roomID}/${this.params.clientID}${window.location.search}`;
      var xhr: XMLHttpRequest = new XMLHttpRequest();
      xhr.open("POST", path, true);
      xhr.send(msgString);
      Util.trace("C->GAE: " + msgString);
    } else {
      this.channel.send(msgString);
    }
  }

  private onError(message: any): void {
    this.dispatchEvent(CallEvent.ERROR, message);
  }

  public getPeerConnectionStats(callback: Function): void {
    if (!BaseUtils.isObjectDefined(this.pcClient)) {
      return;
    }
    this.pcClient.getPeerConnectionStats(callback);
  }

  public getPeerConnectionStates() {
    if (!BaseUtils.isObjectDefined(this.pcClient)) {
      return null;
    }
    return this.pcClient.getPeerConnectionStates();
  }

  public toggleTextChatWindow(): void {
    Util.trace("Text Chat window toggled.");
  }

  public hangup(async: boolean): Promise<any> {
    this.startTime = null;

    if (this.localStream) {
      if (typeof this.localStream.getTracks === "undefined") {
        // Support legacy browsers, like phantomJs we use to run tests.
        this.localStream.stop();
      } else {
        this.localStream.getTracks().forEach((track: any): void => {
          track.stop();
        });
      }
      this.localStream = null;
    }

    if (!this.params.roomID) {
      return;
    }

    if (BaseUtils.isObjectDefined(this.pcClient)) {
      this.pcClient.close();
      this.pcClient = null;
    }

    // Send 'leave' to GAE. This must complete before saying BYE to other client.
    // When the other client sees BYE it attempts to post offer and candidates to
    // GAE. GAE needs to know that we're disconnected at that point otherwise
    // it will forward messages to this client instead of storing them.

    // This section of code is executed in both sync and async depending on
    // where it is called from. When the browser is closed, the requests must
    // be executed as sync to finish before the browser closes. When called
    // from pressing the hang up button, the requests are executed async.

    // If you modify the steps used to hang up a call, you must also modify
    // the clean up queue steps set up in queueCleanupMessages_.');

    var steps: Array<any> = new Array<any>();
    steps.push({
      step: (): any => {
        // Send POST request to /leave.
        const path: string = this.getLeaveUrl();
        return Util.sendUrlRequest("POST", path, async);
      },
      errorString: "Error sending /leave:"
    });
    steps.push({
      step: (): void => {
        // Send bye to the other client.
        this.channel.send(JSON.stringify({ type: "bye" }));
      },
      errorString: "Error sending bye:"
    });
    steps.push({
      step: (): any => {
        // Close signaling channel.
        return this.channel.close(async);
      },
      errorString: "Error closing signaling channel:"
    });
    steps.push({
      step: (): void => {
        this.params.previousRoomID = this.params.roomID;
        this.params.roomID = null;
        this.params.clientID = null;
      },
      errorString: "Error setting params:"
    });

    if (async) {
      var errorHandler: Function = (errorString: string): void => {
        Util.warn(errorString);
      };
      var promise: Promise<any> = Promise.resolve();
      for (let i: number = 0; i < steps.length; ++i) {
        promise = promise
          .then((): void => {
            steps[i].step.call(this);
          })
          .catch((reason: string): void => {
            errorHandler(steps[i].errorString);
          });
      }

      return promise;
    }
    // Execute the cleanup steps.
    var executeStep = (executor: Function, errorString: string): void => {
      try {
        executor();
      } catch (ex) {
        Util.trace(errorString + " " + ex);
      }
    };

    for (var j = 0; j < steps.length; ++j) {
      executeStep(steps[j].step, steps[j].errorString);
    }

    if (this.params.roomID !== null || this.params.clientID !== null) {
      Util.trace("ERROR: sync cleanup tasks did not complete successfully.");
    } else {
      Util.trace("Cleanup completed.");
    }
    return Promise.resolve();
  }

  private clearCleanupQueue(): void {
    // Clear the cleanup queue.
    apprtc.windowPort.sendMessage({ action: Constants.QUEUECLEAR_ACTION });
  }

  private getLeaveUrl(): string {
    return (
      this.roomServer +
      "/leave/" +
      this.params.roomID +
      "/" +
      this.params.clientID
    );
  }

  public onRemoteHangup(): void {
    Util.trace("onRemoteHangup");
    this.startTime = null;

    // On remote hangup this client becomes the new initiator.
    this.params.isInitiator = true;

    if (this.pcClient) {
      this.pcClient.close();
      this.pcClient = null;
    }

    this.startSignaling();

    //this.dispatchEvent(CallEvent.REMOTE_HANGUP);
  }

  private isInitiator(): boolean {
    return this.params.isInitiator;
  }

  public getStartTime(): number {
    return this.startTime;
  }
}
